.section {
	.container {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		flex-flow: row nowrap;

		@include r(1100) {
			flex-flow: column wrap; } } }

.section__title {
	padding: 0 70px;
	align-self: stretch;

	@include r(1100) {
		padding: 50px 30px; }

	h2 {
		font-size: 72px;
		line-height: 1;
		font-weight: 700;
		color: $dark; } }

.section__content {
	width: 100%;
	height: 100%;
	padding: 0; }
