@import url("https://cdnjs.cloudflare.com/ajax/libs/tiny-slider/2.9.2/tiny-slider.css");

// Sliders configs
.tns-controls, .tns-nav, button[data-action=stop] {
	display: none; }

// Courses section
.section--courses {
	margin-bottom: 150px; }

.courses {
	.header__contact {
		&::before {
			max-width: 165px;
			height: 44px; }

		a {
			max-width: 165px;
			height: 44px;
			line-height: 44px; } } }

.courses__sliderLabel {
	width: 70px;
	height: 70px;
	padding: 10px;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	margin-left: 80px;
	color: $white;
	font-size: 14px;
	line-height: 1.15;
	background-color: $dark; }

.courses__sliderWrapper {
	margin-bottom: 30px; }

.courses__slider {
	img {
		display: block;
		width: 100%;
		height: auto; } }
