.section--about {
	padding-bottom: 40px;

	.section__title {
		background-color: $grey-100;
		padding-top: 130px; } }

.about {
	width: 100%;
	height: 100%;
	padding: 80px 140px;

	@include r(1370) {
		padding: 60px 80px; }

	@include r(767) {
		padding: 50px 0; } }

.about__sloganWrapper {
	max-width: 560px;
	width: 100%;
	margin: 0 auto 70px; }

.about__slogan {
	position: relative;
	min-height: 305px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: $dark;
	border-bottom: 4px solid #fff;
	border-right: 4px solid #fff;

	@include r(1100) {
		min-height: 240px; }

	&::before,
	&::after {
		content: '';
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		background-color: $dark;
		border-bottom: 4px solid #fff;
		border-right: 4px solid #fff; }

	&::before {
		top: 24px;
		left: 24px;
		z-index: -1; }

	&::after {
		top: 44px;
		left: 44px;
		z-index: -2; }

	> span {
		font-size: 24px;
		color: $green; } }

.about__text {
	padding: 20px 70px;
	font-size: 24px;
	line-height: 1.25;

	@include r(1370) {
		padding: 20px; }

	p {
		margin-bottom: 25px; } }

.about__textTitle {
	margin-bottom: 30px; }

