.section--review {
	margin-bottom: 100px; }

.review {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between; }

.review__text {
	h3 {
		position: relative;
		font-weight: 700;
		font-size: 24px;
		margin-bottom: 35px;

		&::after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -15px;
			width: 80px;
			height: 4px;
			background-color: $purple; } } }

.review__item {
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: flex-start;
	padding-top: 100px; }

.review__avatarWrapper {
	position: relative;
	padding-right: 70px;

	// TODO: Fix image clip

	&::after {
		content: '';
		position: absolute;
		top: 35px;
		right: 0;
		display: block;
		width: 60px;
		height: 60px;
		background-image: url(../img/icon-quote.svg); } }

.review__textWrapper {
	max-width: 300px;
	width: 100%;
	margin-top: 60px;
	margin-right: 20px; }

.review__feedPagination {
	max-width: 200px;
	width: 100%;
	height: 425px;
	background-color: $dark;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;

	li {
		font-size: 40px;
		line-height: 1.5;
		text-align: center;
		color: $white;
		margin-bottom: 30px;

		&::last-child {
			margin-bottom: 0; }

		&.review__current {
			color: $dark; } } }

.review__feed {
	height: 1300px;
	overflow-y: scroll; }

.review__current {
	width: 120px;
	height: 120px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: $white;
	border-radius: 50%; }

.review__name {
	margin-top: 20px;
	padding-top: 15px;
	border-top: 2px solid $dark;
	font-size: 18px; }
