.core {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-flow: row wrap; }

.core__text, .core__member {
	flex: 0 50%;
	margin-bottom: 70px;

	@include r(800) {
		flex-basis: 100%;
		margin-bottom: 40px; } }

.core__member {
	position: relative;
	padding-left: 40px;

	&:nth-of-type(4) {
		margin-top: 150px;

		@include r(800) {
			margin-top: 40px; } } }

.core__textTeam, .core__textGuest {
	display: inline-block;
	vertical-align: top;
	width: 47%;

	h3 {
		font-size: 24px;
		margin-bottom: 20px; }

	p {
		font-size: 16px;
		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0; } } }

// Core member
.core__member {
	text-align: center; }
.core__avatar {
	border-radius: 50%;
	margin-bottom: 20px; }

.core__name {
	text-align: center;
	font-size: 24px;
	line-height: 1.5; }

.core__position span {
	padding: 3px 20px;
	background-color: $dark-300;
	text-align: center;
	font-size: 13px;
	color: $yellow-200; }

.core__memberSocials {
	position: absolute;
	padding-top: 45px;

	a {
		display: block;
		width: 32px;
		height: 32px;
		padding: 8px;
		text-decoration: none;
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0; } } }

.core__slogan {
	margin-top: 100px;
	font-size: 36px;
	font-weight: 600;
	text-align: left;
	line-height: 1.1; }

.section--core {
	.header__contact {
		margin-top: 20px;

		&::before {
			max-width: 175px;
			width: 100%;
			height: 44px; }

		a {
			max-width: 175px;
			width: 100%;
			height: 44px;
			line-height: 44px; } } }


