.ownCourses {
	width: 100%;
	height: 100%;
	margin-bottom: 200px;

	@include r(767) {
		margin-bottom: 75px; } }

.ownCourses__heading {
	font-size: 24px;
	font-weight: 700; }

.ownCourses__numbersList {
	margin: 0;
	margin-bottom: 80px;
	padding: 0;
	list-style: none;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-flow: row wrap; }

.ownCourses__numbersItem {
	flex: 1 0;

	@include r(767) {
		flex-basis: 50%; } }

.ownCourses__numbersQuantity {
	display: block;
	font-weight: 700;
	font-size: 56px;
	line-height: 1; }

.ownCourses__numbersText {
	font-weight: 400;
	font-size: 16px; }
