.menu {
	display: none;
	position: sticky;
	top: 150px; }

.menu__list {
	list-style: none; }

.menu__item__isactive {
	position: relative;
	padding-left: 40px;

	&::before {
		content: '';
		position: absolute;
		left: 5px;
		top: 50%;
		transform: translateY(-50%);
		display: block;
		width: 30px;
		height: 10px;
		background-color: red; } }


.menu__link {
	text-decoration: none;
	color: $dark;

	&:hover {
		text-decoration: underline; } }
