		background-position: 95% center;
		background-size: 90vh; } }

.hero__slogan {
	font-size: 20px;
	line-height: 1.6;
	font-weight: 400;
	line-height: 1;
	margin-left: 25%;

	& > span {
		display: inline;

		&:first-child {
			display: block;
			font-weight: 700;
			font-size: 84px;
			line-height: 1;
			letter-spacing: -1.5px; } } }

.hero__promo {
	position: absolute; }

.hero__promoText {
	span {
		padding-left: 5px;
		padding-right: 5px;
		color: $white;
		background-color: $dark; } }

.hero__promo--topWhite {
	top: 65%;
	left: 42%; }

.hero__promo--bottomGreen {
	top: 76%;
	left: 40%;
	.hero__promoText > span {
		color: $green; } }

.hero__rebase {
	display: block; }
